@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200;300;400;600;700;900&display=swap");

* {
  box-sizing: border-box;
}

html, body {
  height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  font-family: "Titillium Web",sans-serif;
  background: #e0e0e0;

  
  h2 {
    margin: 0 2rem;
  }

 p {
    margin: 0 2rem;
  }


  main {
    display: flex;
    flex: 1;
    margin: 0 1rem;
  }
}